import React, { useState, useRef } from 'react';
import { motion } from 'framer-motion';
import { gameTypes } from '../data/questions';
import Confetti from 'react-confetti';

interface GameSelectorProps {
  onSelectGame: (gameId: string) => void;
}

const GameSelector: React.FC<GameSelectorProps> = ({ onSelectGame }) => {
  const [selectedGame, setSelectedGame] = useState<string | null>(null);
  const [isSelecting, setIsSelecting] = useState(false);
  const [showConfetti, setShowConfetti] = useState(false);
  const [pinPosition, setPinPosition] = useState(0); // Track pin position
  const barRef = useRef<HTMLDivElement>(null);

  const startSelection = () => {
    if (isSelecting) return;

    setIsSelecting(true);
    const randomIndex = Math.floor(Math.random() * gameTypes.length);
    const selectedGameId = gameTypes[randomIndex].id;
    setSelectedGame(selectedGameId);

    // Move the pin to the selected game
    if (barRef.current) {
      const barWidth = barRef.current.clientWidth;
      const pinWidth = 100; // Adjust based on your pin width
      const position = (randomIndex / gameTypes.length) * (barWidth - pinWidth)+140;
      setPinPosition(position);
    }

    // Call the onSelectGame function after a short delay
    setTimeout(() => {
      onSelectGame(selectedGameId);
      setShowConfetti(true);
      setIsSelecting(false);
    }, 1000);
  };

  // Check if the screen size is small
  const isSmallScreen = window.innerWidth < 768;

  // Define colors for each game type
  const colors = [
    'bg-red-500',
    'bg-blue-500',
    'bg-yellow-500',
    'bg-green-500',
    'bg-purple-500',
    'bg-orange-500',
  ];

  return (
    <div className="relative w-full max-w-4xl mx-auto">
      {showConfetti && <Confetti />}
      
      {!isSmallScreen ? (
        <>
          <div className="flex items-center justify-between mb-4" ref={barRef} style={{ width: '100%' }}>
            {gameTypes.map((game, index) => (
              <div
                key={game.id}
                className={`flex-1 text-center p-4 h-16 flex items-center justify-center border-0 ${colors[index % colors.length]}`} // Use defined colors
              >
                <span className="text-lg font-bold text-white truncate">{game.name}</span>
              </div>
            ))}
          </div>

          {/* Circular Pin for larger screens */}
          <motion.div
            className="absolute w-10 h-10 bg-blue-800 rounded-full shadow-lg"
            style={{ left: pinPosition, transition: 'left 0.5s ease' }} // Move pin based on position
          />
          
          {/* Improved Select Game Button */}
          <button
            onClick={startSelection}
            className="mt-4 w-full h-12 rounded-lg bg-blue-600 text-white font-bold hover:bg-blue-700 transition-colors"
            style={{ fontFamily: 'inherit' }} // Ensure it uses the same font as the rest of the app
          >
            Select Game
          </button>
        </>
      ) : (
        <div className="flex flex-col items-center">
          <button
            onClick={startSelection}
            className="mt-4 w-full h-12 rounded-lg bg-blue-600 text-white font-bold hover:bg-blue-700 transition-colors"
            style={{ fontFamily: 'inherit' }} // Ensure it uses the same font as the rest of the app
          >
            Random Game Selector
          </button>
          {selectedGame && (
            <h3 className="text-xl font-bold text-blue-800 mt-4">
              Selected Game: {gameTypes.find(g => g.id === selectedGame)?.name}
            </h3>
          )}
        </div>
      )}
    </div>
  );
};

export default GameSelector; 