import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const RelationshipTimer: React.FC = () => {
  const [timeTogether, setTimeTogether] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    const startDate = new Date('2024-12-10T00:00:00');
    
    const updateTimer = () => {
      const now = new Date();
      const diff = now.getTime() - startDate.getTime();
      
      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((diff % (1000 * 60)) / 1000);
      
      setTimeTogether({ days, hours, minutes, seconds });
    };

    updateTimer();
    const interval = setInterval(updateTimer, 1000);

    return () => clearInterval(interval);
  }, []);

  const handleLetterClick = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      className="bg-white p-6 rounded-xl shadow-lg text-center flex items-center justify-between"
    >
      <div className="flex-1">
        <h2 className="text-2xl font-bold mb-4">
          Time Together ❤️
        </h2>
        <div className="grid grid-cols-4 gap-4">
          <motion.div className="text-center" animate={{ scale: [1, 1.1, 1] }} transition={{ duration: 1, repeat: Infinity }}>
            <div className="text-3xl font-bold">{timeTogether.days}</div>
            <div className="text-sm">Days</div>
          </motion.div>
          <motion.div className="text-center" animate={{ scale: [1, 1.1, 1] }} transition={{ duration: 1, repeat: Infinity }}>
            <div className="text-3xl font-bold">{timeTogether.hours}</div>
            <div className="text-sm">Hours</div>
          </motion.div>
          <motion.div className="text-center" animate={{ scale: [1, 1.1, 1] }} transition={{ duration: 1, repeat: Infinity }}>
            <div className="text-3xl font-bold">{timeTogether.minutes}</div>
            <div className="text-sm">Minutes</div>
          </motion.div>
          <motion.div className="text-center" animate={{ scale: [1, 1.1, 1] }} transition={{ duration: 1, repeat: Infinity }}>
            <div className="text-3xl font-bold">{timeTogether.seconds}</div>
            <div className="text-sm">Seconds</div>
          </motion.div>
        </div>
      </div>

      {/* Love Letter Section */}
      <div className="flex flex-col items-center ml-4">
        <span role="img" aria-label="love letter" className="text-3xl cursor-pointer" onClick={handleLetterClick}>
          💌
        </span>
        <div className="text-sm text-gray-700 text-left mt-2">
          <p>Click the letter to read a message!</p>
        </div>
      </div>

      {/* Modal for Love Letter */}
      {isModalOpen && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
          <div className="bg-white p-6 rounded-lg shadow-lg text-center z-60">
            <h3 className="text-lg font-bold">Dear Roshni,</h3>
            <p>Your love is the most precious thing in my life.</p>
            <p>Every moment together is a treasure!</p>
            <button onClick={closeModal} className="mt-4 bg-blue-600 text-white px-4 py-2 rounded">
              Close
            </button>
          </div>
        </div>
      )}
    </motion.div>
  );
};

export default RelationshipTimer; 