import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { gameTypes } from './data/gameData';
import GameContent from './components/GameContent';
import GameWheel from './components/GameWheel';
import RelationshipTimer from './components/RelationshipTimer';

interface Player {
  name: string;
  avatar: string;
}

const App: React.FC = () => {
  const [players] = useState<Player[]>([
    { name: 'Heet', avatar: '👦🏻' },
    { name: 'Roshni', avatar: '👧🏻' }
  ]);
  const [currentGame, setCurrentGame] = useState<string | null>(null);
  const [gameStarted, setGameStarted] = useState(false);
  const [showWheel, setShowWheel] = useState(true);

  const startGame = (gameId: string) => {
    setCurrentGame(gameId);
    setGameStarted(true);
    setShowWheel(false);
  };

  const handleWheelSelect = (gameId: string) => {
    setCurrentGame(gameId);
    setGameStarted(true);
    setShowWheel(false);
  };

  const goBack = () => {
    setGameStarted(false);
    setCurrentGame(null);
    setShowWheel(true);
  };

  return (
    <div className="min-h-screen p-8 bg-gray-100">
      <div className="max-w-6xl mx-auto">
        <RelationshipTimer />
        
        {!gameStarted ? (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="mt-8"
          >
            <h1 className="text-5xl font-display font-bold text-center mb-8 text-transparent bg-clip-text bg-gradient-to-r from-blue-500 to-green-500">
              BondQuest
            </h1>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-12">
              {players.map((player, index) => (
                <motion.div
                  key={index}
                  className="card bg-white/80 backdrop-blur-sm"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="text-6xl mb-4">{player.avatar}</div>
                  <h2 className="text-2xl font-bold mb-2">{player.name}</h2>
                  <p className="text-gray-600">Player {index + 1}</p>
                </motion.div>
              ))}
            </div>

            <div className="mb-8">
              <h2 className="text-2xl font-bold text-center mb-4">Choose a Game</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {gameTypes.map((game) => (
                  <motion.div
                    key={game.id}
                    className="card bg-white/80 backdrop-blur-sm cursor-pointer"
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => startGame(game.id)}
                  >
                    <h3 className="text-xl font-bold mb-2">{game.name}</h3>
                    <p className="text-gray-600">{game.description}</p>
                  </motion.div>
                ))}
              </div>
            </div>

            <AnimatePresence>
              {showWheel ? (
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                >
                  <GameWheel onSelectGame={handleWheelSelect} />
                </motion.div>
              ) : null}
            </AnimatePresence>
          </motion.div>
        ) : (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="mt-8"
          >
            <button
              className="btn-secondary mb-8"
              onClick={goBack}
            >
              Back to Games
            </button>
            
            {currentGame && (
              <GameContent
                gameId={currentGame}
                players={players}
              />
            )}
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default App;
