import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  gameTypes, 
  neverHaveIEverQuestions, 
  wouldYouRatherQuestions, 
  truthOrDareQuestions, 
  thisOrThatQuestions, 
  deepQuestions, 
  funnyPrompts 
} from '../data/questions';

interface GameContentProps {
  gameId: string;
  players: { name: string; avatar: string }[];
}

const GameContent: React.FC<GameContentProps> = ({ gameId, players }) => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [currentPlayerIndex, setCurrentPlayerIndex] = useState(0);
  const [showAnswer, setShowAnswer] = useState(false);
  const [gameQuestions, setGameQuestions] = useState<any[]>([]);

  useEffect(() => {
    let questions: any[] = [];
    switch (gameId) {
      case 'never-have-i-ever':
        questions = neverHaveIEverQuestions;
        break;
      case 'would-you-rather':
        questions = wouldYouRatherQuestions;
        break;
      case 'truth-or-dare':
        questions = truthOrDareQuestions;
        break;
      case 'this-or-that':
        questions = thisOrThatQuestions;
        break;
      case 'deep-questions':
        questions = deepQuestions;
        break;
      case 'funny-prompts':
        questions = funnyPrompts;
        break;
      default:
        break;
    }

    const shuffledQuestions = [...questions].sort(() => Math.random() - 0.5);
    setGameQuestions(shuffledQuestions);
  }, [gameId]);

  const currentQuestion = gameQuestions[currentQuestionIndex];
  const currentPlayer = players[currentPlayerIndex];

  const nextQuestion = () => {
    setShowAnswer(false);
    setCurrentQuestionIndex((prev) => (prev + 1) % gameQuestions.length);
    setCurrentPlayerIndex((prev) => (prev + 1) % players.length);
  };

  if (!currentQuestion) return null;

  return (
    <div className="game-background space-y-8">
      <div className="flex flex-col items-center mb-8">
        <span className="text-4xl">{currentPlayer.avatar}</span>
        <span className="text-xl font-bold">{currentPlayer.name}'s turn</span>
        <span className="text-sm text-gray-500">
          Question {currentQuestionIndex + 1} of {gameQuestions.length}
        </span>
      </div>

      <h2 className="text-3xl font-bold text-center mb-4">
        {gameTypes.find(g => g.id === gameId)?.name}
      </h2>

      <AnimatePresence mode="wait">
        <motion.div
          key={currentQuestion.id}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -20 }}
          className="card p-8 text-center"
        >
          <h3 className="text-2xl font-bold mb-4">{currentQuestion.text}</h3>
          
          <div className="mt-8 space-y-4">
            {gameId === 'never-have-i-ever' && (
              <>
                <button
                  className="btn-primary w-full"
                  onClick={() => {
                    setShowAnswer(true);
                  }}
                >
                  I Have!
                </button>
                <button
                  className="btn-secondary w-full"
                  onClick={() => {
                    setShowAnswer(true);
                  }}
                >
                  Never!
                </button>
              </>
            )}
            
            {gameId === 'would-you-rather' && (
              <>
                <button
                  className="btn-primary w-full"
                  onClick={() => {
                    setShowAnswer(true);
                  }}
                >
                  I Would
                </button>
                <button
                  className="btn-secondary w-full"
                  onClick={() => {
                    setShowAnswer(true);
                  }}
                >
                  I Would Not
                </button>
              </>
            )}
          </div>
        </motion.div>
      </AnimatePresence>

      {showAnswer && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="flex justify-center"
        >
          <button
            className="btn-primary mt-4 w-40 h-12 rounded-lg bg-blue-600 text-white font-bold hover:bg-blue-700 transition-colors flex items-center justify-center"
            onClick={nextQuestion}
          >
            Next Question
          </button>
        </motion.div>
      )}

      {currentQuestionIndex < (gameId === 'never-have-i-ever' ? neverHaveIEverQuestions.length : 
                                gameId === 'would-you-rather' ? wouldYouRatherQuestions.length : 
                                gameId === 'truth-or-dare' ? truthOrDareQuestions.length : 
                                gameId === 'this-or-that' ? thisOrThatQuestions.length :
                                gameId === 'deep-questions' ? deepQuestions.length :
                                gameId === 'funny-prompts' ? funnyPrompts.length : 0) - 1 && !showAnswer && (
        <button
          onClick={nextQuestion}
          className="btn-primary mt-4 w-40 h-12 rounded-lg bg-blue-600 text-white font-bold hover:bg-blue-700 transition-colors flex items-center justify-center"
        >
          Next Question
        </button>
      )}
    </div>
  );
};

export default GameContent; 